import React from "react";
import $ from "jquery";

function Enlarged(props) {
  var i=props.index;
  var arr=props.arr;
  const saveBlog = (e) => {
    $("body").removeClass("editing");
    $("#h1").html(`Title: ` + $(".title-update").val());
    $("#h2").html(`Author: ` + $(".author-update").val());
    $("#h3").html(`Isbn: ` + $(".isbn-update").val());
  };
  const editBlog = (e) => {
  
    $("body").addClass("editing")   
     var title = (arr[i].title);
 console.log(title);
 var author = (arr[i].author);
 var isbn = (arr[i].isbn);
    $("#h1").html(
      '<input type="text" class="form-control title-update" value="' +
        title +
        '">'
    );

    $("#h2").html(
      '<input type="text" class="form-control author-update" value="' +
        author +
        '">'
    );

    $("#h3").html(
        '<input type="text" class="form-control isbn-update" value="' +
        isbn +
        '">'
    );
  };
  return (
  
    <React.Fragment>
      
      <div className="container mt-4">
        <h1 id="heading" className="display-4 text-center">
          <i class="fas fa-bolt text-primary"></i> My
          <span class="text-primary">Book</span>List
        </h1>
      </div>
      <div id="cards" className="container mt-4">
        <div className="card">
          <div className="card-header">
            <img src={arr[i].url} id="img4"></img>
          </div>
          <div className="card-body">
            
            <h1 id="h1">Title: {arr[i].title} </h1>
            <h1 id="h2">Author: {arr[i].author} </h1>
           
            <h1 id="h3">Isbn: {arr[i].isbn}</h1>
          </div>
        </div>
       
       <center> <button className="btn btn-success btn-lg" id="SAVE" onClick={saveBlog}>
          Save
        </button></center>
        <center><button className="btn btn-info btn-lg" id="EDIT" onClick={editBlog}>
          Edit
        </button></center>
      </div>
    </React.Fragment>
  );
}

export default Enlarged;